'use client'
import '@/styles/fv/fv.css'
import { useEffect, useRef, useState } from 'react'
import { gsap } from 'gsap'

const FV = () => {
  const containerRef = useRef<HTMLDivElement | null>(null)
  const [itemCount, setItemCount] = useState(16)

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth <= 768) {
        setItemCount(12)
      } else {
        setItemCount(16)
      }
    }

    handleResize()
    window.addEventListener('resize', handleResize)
    return () => window.removeEventListener('resize', handleResize)
  }, [])

  useEffect(() => {
    const container = containerRef.current
    if (!container) return

    const items = Array.from(container.querySelectorAll('.gridItem'))

    const shuffled = items.sort(() => Math.random() - 0.5)

    shuffled.forEach((item, i) => {
      gsap.to(item, {
        opacity: 1,
        delay: i * 0.1,
        duration: 0.4,
        ease: 'power2.out',
      })
    })
  }, [itemCount])

  return (
    <section className="fv">
      <div ref={containerRef} className="gridContainer">
        {Array.from({ length: itemCount }).map((_, index) => (
          <div key={index} className="gridItem">
            <img
              src={`/images/section/FVSection/fv${index + 1}.jpg`}
              alt="image"
            />
          </div>
        ))}
      </div>
    </section>
  )
}

export default FV
